import React, { Component } from 'react';
import { Row } from 'reactstrap';
import { Doughnut } from 'react-chartjs-2';
class Tech extends Component {
  render() {
    const dataset = {
      labels: ['Node.js', 'PHP', 'Go', 'Python'],
      datasets: [
        {
          data: [60, 20, 3, 5],
          backgroundColor: ['#007800', '#637b80', '#f47f42', '#2faee0']
        }
      ]
    };
    return (
      <Row>
        <h3 style={{ paddingLeft: '15px' }}>Programming Languages</h3>
        <Doughnut data={dataset} />

        <h3 style={{ paddingLeft: '15px' }}>JavaScript Stack</h3>
        <Doughnut
          data={{
            labels: ['ExpressJS', 'ReactJS', 'Sails.JS'],
            datasets: [
              {
                data: [60, 30, 15],
                backgroundColor: ['#007800', '#637b80', '#f47f42']
              }
            ]
          }}
        />

        <h3 style={{ paddingLeft: '15px' }}>Other technologies</h3>
        <Doughnut
          data={{
            labels: ['REST', 'GraphQL', 'Database (PostgreSQL, MySQL)', 'Bots (Lex/Alexa)', 'DevOps'],
            datasets: [
              {
                data: [60, 30, 50, 15, 5],
                backgroundColor: ['#007800', '#777c26', '#637b80', '#f47f42', '#434745']
              }
            ]
          }}
        />
      </Row>
    );
  }
}

export default Tech;



